.contrasto-on {

    :host::ng-deep div[ngbdatepickerdayview] {
        color: @black;
    }

    :host::ng-deep .bg-primary :not(.disabledView){
        color: @white !important;
        background-color: @black !important;
    }

    :host::ng-deep div[ngbdatepickerreplymonthview] {
        color: @black;
        border: #d3d3d3 solid 1px;
    }

    :host::ng-deep div[ngbdatepickerreplymonthview]:hover .ngbdatepickerreplymonthview{
        background-color: @black;
        color: @white;
    }
    :host::ng-deep div[ngbdatepickerreplyyearsview] {
        color: @black;
        border: #d3d3d3 solid 1px;
    }

    :host::ng-deep div[ngbdatepickerreplyyearsview]:hover .ngbDatepickerReplyYearsView{
        background-color: @black;
        color: @white;
    }

    :host::ng-deep .ngb-dp-month-name {
        color: @black;
    }

    :host::ng-deep .ngb-dp-weekda{
        color: @black!important;
    }

    :host::ng-deep .ngbdatepickerdayviewdisabled {
        color: @white!important;
        background-color: @black!important;
        opacity: 0.5;
    }

    :host::ng-deep .ngb-dp-navigation-chevron::before {
        color: @black!important;
    }

    :host::ng-deep .ngb-dp-weekday{
        color: @black!important;
    }

    :host::ng-deep .ngbdatepickerreplymonthviewdisabled{
        color: @black!important;
    }

    :host::ng-deep .ngbDatepickerReplyYearsViewdisabled {
        color: @black!important;
    }
}

:host-context(.contrasto-on) {
    .button-icon {
        color: @black;
    }
}
